*{
	margin: 0px;
	padding: 0px;
}
body{
	background:  -webkit-gradient(linear, left bottom, left top, from(#faefee), to(#f4f4e4));
	background:  -webkit-linear-gradient(bottom, #faefee, #f4f4e4);
	background:  -o-linear-gradient(bottom, #faefee, #f4f4e4);
	background:  linear-gradient(0deg, #faefee, #f4f4e4);
     font-size: 18px;
     font-family: helvetica, verdana, sans-serif;
  
}
@media (max-width:760px){
	body{
		 font-size: 8px;
	}
}

a{
	color: black;
	text-decoration: none;

}  
ul {
	list-style: none;
}




